<template>
  <div>
    <header>
      <table>
        <tr class="title" style="font-size:1.3rem;">
          {{
            product_code
          }}
        </tr>
        <thead>
          <tr>
            <th>序号</th>
            <th>产品编码</th>
            <th>站点</th>
            <th>国家</th>
            <th>sku</th>
            <th>品名</th>
            <th>状态</th>
            <th>库存数量</th>
            <th>日均单量</th>
            <th>月均单量</th>
            <th>总单量</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableList" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ item.product_code }}</td>
            <td>{{ item.the_store }}</td>
            <td>{{ item.countries }}</td>
            <td>{{ item.sku }}</td>
            <td>{{ item.product_name }}</td>
            <td>在售</td>
            <td>{{ item.inventory }}</td>
            <td>{{ item.avg_day_sku.toFixed(2) }}</td>
            <td>{{ item.avg_sku.toFixed(2) }}</td>
            <td>{{ item.sum_sku }}</td>
          </tr>
        </tbody>
      </table>
    </header>
    <footer style="margin: 0 12vw;">
      <h3 style="text-align: center; color:#eaf7e0;">配件详情</h3>
      <!-- <table>
              <thead>
                  <tr>
                      <th>配件名称</th>
                      <th>数量</th>
                  </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in bottomTableList" :key="index">
                    <td>{{ item[0] }}</td>
                    <td>{{ item[1] }}</td>
                </tr>
              </tbody>
          </table> -->
      <div v-for="(item, index) in bottomTableList" :key="index">
        <div>产品名称： {{ item.part_name }}</div>
        <div>编码：{{ item.part_code }}</div>
        <div>数量：{{ item.part_num }}</div>
      </div>
    </footer>
    <el-button type="warning" @click="goProductInformation()" class="returnBtn"
      >返 回</el-button
    >
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      product_code: "", //路由传过来的产品编码
      tableList: [], //表格数据
      bottomTableList: [] //下方表格数据,
      // queryObj: {} //
    };
  },
  methods: {
    getDefaultTableList() {
      console.log(this.product_code);
      axios({
        url: this._url2 + "server/databases/product_information_detail",
        method: "get",
        params: {
          product_code: this.product_code
        }
      }).then(res => {
        console.log(res);
        this.bottomTableList = res.data.access_data;
        this.bottomTableList.splice(0, 1);
        console.log(this.bottomTableList);
        for (let i = 0; i < res.data.date_data.length; i++) {
          for (let j = 0; j < res.data.product_data.length; j++) {
            if (res.data.product_data[j].sku) {
              if (
                res.data.date_data[i].sku == res.data.product_data[j].sku &&
                res.data.date_data[i].countries ==
                  res.data.product_data[j].countries
              ) {
                res.data.date_data[i]["inventory"] =
                  res.data.product_data[j].fba;
                break;
              } else {
                res.data.date_data[i]["inventory"] = 0;
              }
            } else {
            }
          }
        }
        this.tableList = res.data.date_data;
        console.log(this.tableList);
      });
    },
    goProductInformation() {
      this.$router.push({
        path: "/ProductInformation"
        // query: {
        //   queryObj: JSON.stringify(this.queryObj)
        // }
      });
    }
  },
  mounted() {
    this.product_code = this.$route.query.id;
    // this.queryObj = this.$route.query.queryObj;

    this.getDefaultTableList();
  }
};
</script>

<style scoped>
table {
  /*width: 84vw;*/
  margin: 10px auto;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 80vh;
  width: fit-content;
  overflow-y: auto;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 40px;
  line-height: 40px;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 40px;
  line-height: 40px;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

.title {
  border-top: #e5e5e5 1px solid;
  border-left: #e5e5e5 1px solid;
  border-right: #e5e5e5 1px solid;
  height: 40px;
  line-height: 40px;
  text-align: center;
}

header table {
  margin: 30px auto;
}

header table tr td:nth-child(1),
header table tr th:nth-child(1) {
  width: 3vw;
}

header table tr td:nth-child(2),
header table tr th:nth-child(2),
header table tr td:nth-child(4),
header table tr th:nth-child(4) {
  width: 5vw;
}

header table tr td:nth-child(8),
header table tr th:nth-child(8),
header table tr td:nth-child(9),
header table tr th:nth-child(9),
header table tr td:nth-child(10),
header table tr th:nth-child(10),
header table tr td:nth-child(11),
header table tr th:nth-child(11),
header table tr td:nth-child(12),
header table tr th:nth-child(12) {
  width: 6vw;
}

header table tr td:nth-child(3),
header table tr th:nth-child(3) {
  width: 8vw;
}

header table tr td:nth-child(7),
header table tr th:nth-child(7) {
  width: 6vw;
}

header table tr td:nth-child(6),
header table tr th:nth-child(6),
header table tr td:nth-child(5),
header table tr th:nth-child(5) {
  width: 12.5vw;
}

.trBottom {
  border-bottom: 1px #e5e5e5 solid;
}

/* footer table{
        margin-top: 5vw;
        margin-right: 58vw;
    } */

.divBottom {
  border-bottom: 1px #e5e5e5 solid;
}

td div {
  height: 40px;
}

footer > div {
  display: inline-block;
  width: 19%;
  border: 1px #dddddd solid;
  margin: 0.5vh;
  background: rgba(255, 255, 255, 0.9);
}

footer > div:hover {
  background-color: #eaf7e0;
}
footer > div div:nth-child(1) {
  height: 6vh;
}
.returnBtn {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 10vw;
}
</style>



